@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
body {
  height: 100vh;
  color: white;
  margin: 0;
  font-family: 'Orbitron', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 1.3rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: rgb(55,12,42);
  background: linear-gradient(45deg, rgba(55,12,42,1) 24%, rgba(124,22,37,1) 69%, rgba(201,82,82,1) 97%); 
}

.container {
  width: 400px;
  height: 500px;
  padding: 2px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  background: #2d3436;
  grid-template-areas: 
  "display display display display"
  "clear clear divide multiply"
  "seven eight nine subtract"
  "four five six add"
  "one two three equals"
  "zero zero decimal equals"
}

.display-wrapper {
  grid-area: display;
  display: -webkit-flex;
  display: flex;
  height: 60px;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding-right: 6px;
  padding-top: 5px;
}
#clear {grid-area: clear;}
#divide {grid-area: divide;}
#multiply {grid-area: multiply;}
#seven {grid-area: seven;}
#eight {grid-area: eight;}
#nine {grid-area: nine;}
#subtract {grid-area: subtract;}
#four {grid-area: four;}
#five {grid-area: five;}
#six {grid-area: six;}
#add {grid-area: add;}
#one {grid-area: one;}
#two {grid-area: two;}
#three {grid-area: three;}
#equals {grid-area: equals;}
#zero {grid-area: zero;}
#decimal {grid-area: decimal;}

.input {
  min-height: 21px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

#display {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

button {
  background: rgb(41, 43, 44); 
  color: white;
  font-size: 1.5rem;
  outline: 0;
  border: 1px solid black;
  min-height: 86px;
}

button:hover {
  cursor: pointer;
  border: 2px solid white;
}

#clear {
  background: #f55454;
}

#equals {
  background: #0984e3;
}

#divide, #multiply, #subtract, #add {
  background: rgb(190, 129, 36);
}

.author {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 8px;
}

.author > a {
  text-decoration: none;
  color: white;
}

.author > a:hover {
  cursor: pointer;
}
